<template>
  <yd-navbar slot="navbar" :title="title" bgcolor="#4A5B79" color="#fff" fontsize="0.28rem">
    <section slot="left" v-if="showLeft" @click="$router.go(-1)">
      <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
    </section>

    <slot name="right" slot="right">
      <yd-icon name="home-outline" size=".4rem" @click.native="$router.push({path:'/'})"></yd-icon>
    </slot>
  </yd-navbar>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'BNavbar',
    props: {
      showLeft: {
        default: false,
        type: Boolean
      },
      title: {
        default: 'tools',
        type: String
      }
    }
  }
</script>

<style type="text/css" scoped>

</style>



